* { box-sizing: border-box; } 
.App { position: fixed; display:flex; top: 0; left: 0; width: 100%; height: 100%; background-color: #000; overflow: hidden; align-items: center; justify-content: center; } 
.Panel-Container { display: flex; max-width: 1200px; width: 100%; height: 100%; padding: 30px; flex-direction: column; flex: 2px; } 
.Panel-Row { display:flex; width: 100%; height: 100%; } 
.Panel-Row:nth-child(2) { height: 35%; } 
.Panel-Row:nth-child(n+2) { margin-top: 2px; } 
.Panel-Bundle { flex: 1 1; } 
.Panel-Bundle:nth-child(n+2) { margin-left: 2px; } 
.Panel-Grid { display: grid; width: 100%; height:100%; gap: 2px; } 
.Panel-Grid-3 { grid-template-columns: auto auto auto; } 
.Panel-Grid-4 { grid-template-rows: auto auto; grid-template-columns: auto auto; } 
.Panel { width: 100%; height: 100%; background-color: #fff; border-radius: 10px; cursor: pointer; } 

.Modal {position: fixed; display: flex; top:0; left:0; width: 100%; height: 100%; align-items: center; justify-content: center;}
.Modal-Background {position: fixed; top:0; left:0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.6);}
.TempModal { width: 100px; height: 100px; background-color: #ddd; border-radius: 10px; z-index: 9;}